export const getsectionId = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_section.method,
      path: api.GET_section.path,
      query: params,
    }).then((response) => {
      dispatch({
        type: "GET_section_DETAIL",
        selected: response?.data,
      });
    });
  };
};

export const getQuiz = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_QUIZ.method,
      path: api.GET_QUIZ.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_QUIZ",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};
export const getKindId = (categoryId) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_KIND.method,
      path: api.GET_KIND.path,
      query: { id: categoryId },
    })
      .then((response) => {
        dispatch({
          type: "GET_KindId",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};
export const addQuiz = (Document) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_QUIZ.method,
      path: api.CREATE_QUIZ.path,
      data: Document,
    })
      .then((response) => {
        dispatch({
          type: "ADD_QUIZ",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_QUIZ",
          err,
        })
      );
  };
};
export const updateQuiz = (Category) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_QUIZ.method,
      path: api.UPDATE_QUIZ.path,
      data: Category,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_QUIZ",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_DOCUMENT_COURSE",
          err,
        })
      );
  };
};
