import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";

import "react-phone-number-input/style.css";

import { isObjEmpty, scrollToTop } from "@utils";

import classnames from "classnames";
import { Controller, useForm } from "react-hook-form";

import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "@constants/validate";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { convertToRaw, EditorState } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import { htmlToDraftUtil } from "@utils";
import Select from "react-select";
import {} from "react-scroll-up";
import { getSection, getsectionId, updateQuiz } from "../store/action";
import draftToHtml from "draftjs-to-html";
import { validateEditor } from "../../../../../helper/common";
import classNames from "classnames";
import { update as updateIsQuizSection } from "../../../product/term/store/action";

const CourseDocumentTab = ({ selected, intl }) => {
  const product = useSelector((state) => state?.products?.data);
  const store = useSelector((state) => state.quiz);
  const history = useHistory();
  const [categoryData, setCategoryData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const [editorState, setEditorState] = useState(null);
  const dispatch = useDispatch();
  const [status, setStatus] = useState(null);
  const [publish, setPublish] = useState(null);
  const [disable, setDisable] = useState(false);
  const [section, setSection] = useState([]);
  const [changcate, setChangcate] = useState();
  const { id } = useParams();
  const [changeSelect, setChangeSelect] = useState(changcate);
  const [changeSelectProduct, setChangeSelectProduct] = useState();
  const [changeSelectSection, setChangeSelectSection] = useState();

  const EditQuizOptions = validateOptions.EditQuizOptions;
  const [isSubmit, setIsSubmit] = useState(false);

  const [Correct_answer, setCorrect_answer] = useState(
    EditorState.createEmpty()
  );

  const document_course = yup.object({
    productId: yup
      .number()
      .required(
        <FormattedMessage id="The course field is requiredis required" />
      ),
    curriculumSectionId: yup
      .number()
      .required(<FormattedMessage id="The term field is required" />),
    contenttext: yup
      .string()
      .required(<FormattedMessage id="Question field is required" />),
    answer: yup
      .string()
      .required(<FormattedMessage id="The answer field is required" />),
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    getValues,
    clearErrors,
    setValue,
  } = useForm({
    // resolver: yupResolver(document_course),
    mode: "all",
  });

  const [optionProductValue, setOptionProductValue] = useState(null);

  const [optionSectionValue, setOptionSectionValue] = useState(null);

  const [valueContent, setValueContent] = useState("");

  const [valueShortContent, setValueShortContent] = useState(
    EditorState.createEmpty()
  );

  useEffect(() => {
    dispatch(
      getsectionId({
        id: selected?.curriculumSectionId,
      })
    );
  }, [dispatch, selected?.curriculumSectionId]);

  useEffect(() => {
    dispatch(
      getSection({
        filter: {},
        skip: 0,
        limit: 20,
        order: [],
      })
    );
  }, [dispatch]);

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        categoryData !== null &&
        selected.id !== categoryData.id)
    ) {
      setCategoryData(selected);
      setValue("productId", selected?.productId);
      setValue("curriculumSectionId", selected?.curriculumSectionId);

      setOptionProductValue({
        value: selected?.product?.id || 0,
        label: selected?.product?.product_names[0]?.name || "",
      });
    }
  }, [selected, categoryData]);

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        store?.sectionDetail?.id &&
        store?.sectionDetail?.title)
    ) {
      if (
        store?.sectionDetail?.id !== undefined &&
        store?.sectionDetail?.title !== undefined
      ) {
        setOptionSectionValue({
          value: store?.sectionDetail?.id,
          label: store?.sectionDetail?.title,
        });
      }
    }
  }, [selected, store?.sectionDetail]);

  useEffect(() => {
    if (selected !== null || selected !== null) {
      if (product?.length > 0) {
        product?.forEach((element) => {
          if (element?.id === optionProductValue?.value) {
            setSection(element?.sections);
          }
        });
      }
    }
  }, [selected, product?.length, optionProductValue?.value]);

  useEffect(() => {
    if (store.status === 200) {
      scrollToTop();
      history.push("/apps/course/test/list");
    }
  }, [store.status, history]);

  const onSubmit = async (values) => {
    setIsSubmit(true);
    if (validateEditor(valueContent)) {
      setEditorState({
        classError: "invalid-editor is-invalid form-control",
        correct_answerError: (
          <FormattedMessage id="The correct_answer is required" />
        ),
      });
    } else {
      setEditorState(null);

      const description =
        (valueContent &&
          draftToHtml(convertToRaw(valueShortContent?.getCurrentContent()))) ||
        null;

      // if (isObjEmpty(errors)) {
      setDisable(true);
      dispatch(
        updateQuiz({
          id: parseInt(id),
          data: {
            ...values,
            status: parseInt(values.status),
            publish: parseInt(values.publish),
            description,
            correct_answer: valueContent,
            lang,
          },
        })
      );

      if (parseInt(values.status) === 1 && parseInt(values.publish) === 1) {
        if (store?.sectionDetail?.lectures_quizs?.length < 2) {
          dispatch(
            updateIsQuizSection({
              id: selected?.curriculumSectionId,
              data: {
                isQuiz: 0,
                status: 1,
                lang,
                progress: 1,
              },
            })
          );
        }
        dispatch(
          updateIsQuizSection({
            id: selected?.curriculumSectionId,
            data: {
              isQuiz: 1,
              status: 1,
              lang,
              progress: 1,
            },
          })
        );
      }
      // }
    }
  };

  useEffect(() => {
    if (Correct_answer) {
      const description =
        Correct_answer &&
        draftToHtml(convertToRaw(Correct_answer.getCurrentContent()));
      setValueContent(description);
    }
  }, [Correct_answer]);
  useEffect(() => {
    if (categoryData) {
      setCorrect_answer(htmlToDraftUtil(categoryData?.correct_answer || " "));
      setValueShortContent(htmlToDraftUtil(categoryData?.description || " "));
      setStatus(categoryData?.status);
      setPublish(categoryData?.publish);
    }
  }, [categoryData]);

  useEffect(() => {
    if (isSubmit) {
      const check = validateEditor(valueContent);
      if (check === true) {
        setEditorState({
          classError: "invalid-editor is-invalid form-control",
          descriptionError: (
            <FormattedMessage id="The correct_answer is required" />
          ),
        });
      } else {
        setEditorState({});
      }
    }
  }, [valueContent, isSubmit]);

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.name} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="sort_order">
              <FormattedMessage id="sort_order" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="sort_order"
              id="sort_order"
              innerRef={register(EditQuizOptions.sort_order)}
              onBlur={() => {
                let sort_order = document.getElementById("sort_order");
                if (sort_order && sort_order.value) {
                  sort_order.value = sort_order.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["sort_order"] })}
              defaultValue={categoryData && categoryData.sort_order}
            />
            <small className="text-danger">
              {errors?.sort_order && errors.sort_order.message}
            </small>
            {errors?.sort_order?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="contenttext">
              <FormattedMessage id="question" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="contenttext"
              id="contenttext"
              innerRef={register(EditQuizOptions.contenttext)}
              onBlur={() => {
                let contenttext = document.getElementById("contenttext");
                if (contenttext && contenttext.value) {
                  contenttext.value = contenttext.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["contenttext"] })}
              defaultValue={categoryData && categoryData.contenttext}
            />
            <small className="text-danger">
              {errors?.contenttext && errors.contenttext.message}
            </small>
            {errors?.contenttext?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="answer">
              <FormattedMessage id="Right answer" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="answer"
              id="answer"
              innerRef={register(EditQuizOptions.answer)}
              onBlur={() => {
                let answer = document.getElementById("answer");
                if (answer && answer.value) {
                  answer.value = answer.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["answer"] })}
              defaultValue={categoryData && categoryData.answer}
            />
            <small className="text-danger">
              {errors?.answer && errors.answer.message}
            </small>
            {errors?.answer?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="correct_answer">
              <FormattedMessage id="Options" />{" "}
              <span className="text-danger"></span>
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                options: ["inline", "textAlign"],
                inline: {
                  inDropdown: false,
                  options: ["bold", "italic", "underline"],
                },
              }}
              defaultEditorState={Correct_answer}
              editorState={Correct_answer}
              onEditorStateChange={(data) => setCorrect_answer(data)}
              toolbarClassName="toolbarClassName"
              wrapperClassName={"wrapperClassName " + editorState?.classError}
              editorClassName="editorClassName "
              name="correct_answer"
              className={classNames({
                "is-invalid": errors["correct_answer"],
              })}
            />
            {editorState && (
              <small className="text-danger">
                {editorState.descriptionError}
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="description">
              <FormattedMessage id="description" />{" "}
              <span className="text-danger"></span>
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                options: ["inline", "textAlign"],
                inline: {
                  inDropdown: false,
                  options: ["bold", "italic", "underline"],
                },
              }}
              defaultEditorState={valueShortContent}
              editorState={valueShortContent}
              onEditorStateChange={(data) => setValueShortContent(data)}
              name="description"
              // innerRef={register(Document_course.short_content)}
            />
            <small className="text-danger">
              {errors?.description && errors.description.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label>
              <FormattedMessage id="Course" />
              <span className="text-danger"> * </span>
            </Label>

            {product?.length > 0 && optionProductValue && (
              <Controller
                control={control}
                name="productId"
                render={({ field }) => {
                  return (
                    <Select
                      id="productId"
                      innerRef={register}
                      name="productId"
                      placeholder={<FormattedMessage id="Select Course" />}
                      className={classnames(
                        "react-select",
                        !changeSelectProduct
                          ? {
                              "is-invalid": errors["productId"],
                            }
                          : ""
                      )}
                      defaultValue={optionProductValue}
                      options={product?.map((item, index) => {
                        return {
                          value: item?.id,
                          label: item?.product_names[0]?.name || "",
                          section: item?.sections || [],
                        };
                      })}
                      classNamePrefix="select"
                      {...field}
                      onChange={(e) => {
                        clearErrors("productId");
                        setValue("productId", e?.value);
                        setChangeSelectProduct(e?.value);
                        setOptionProductValue(e);

                        setValue("curriculumSectionId", undefined);
                        setChangeSelectSection(null);
                        setOptionSectionValue(undefined);
                      }}
                    />
                  );
                }}
              ></Controller>
            )}

            <small className="text-danger">
              {errors?.productId && errors.productId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label>
              <FormattedMessage id="term" />
              <span className="text-danger"> * </span>
            </Label>
            {section?.length > 0 && optionSectionValue !== null && (
              <Controller
                control={control}
                name="curriculumSectionId"
                render={({ field }) => {
                  return (
                    <Select
                      id="curriculumSectionId"
                      innerRef={register}
                      name="curriculumSectionId"
                      placeholder={<FormattedMessage id="Select..." />}
                      className={classnames(
                        "react-select",
                        !changeSelectSection
                          ? {
                              "is-invalid": errors["curriculumSectionId"],
                            }
                          : ""
                      )}
                      defaultValue={optionSectionValue}
                      options={section?.map((item, index) => {
                        return {
                          value: item?.id,
                          label: item?.title,
                        };
                      })}
                      classNamePrefix="select"
                      {...field}
                      onChange={(e) => {
                        clearErrors("productId");
                        clearErrors("curriculumSectionId");

                        // setError("curriculumSectionId", "");
                        setValue("curriculumSectionId", e?.value);
                        setChangeSelectSection(e?.value);
                        setOptionSectionValue(e);
                      }}
                    />
                  );
                }}
              ></Controller>
            )}

            {/* <Select
              isClearable={false}
              onChange={(e) => {
                setError("curriculumSectionId", "");
                setValue("curriculumSectionId", e?.value);
                setChangeSelectProduct(e?.value);
                setOptionProductValue(e);
                // changeCate()
                // setSection(e?.section);
              }}
              innerRef={register({ required: true })}
              name="curriculumSectionId"
              defaultValue={optionSectionValue}
              placeholder={<FormattedMessage id="Select..." />}
              options={section?.map((item, index) => {
                return {
                  value: item?.id,
                  label: item?.title,
                };
              })}
              className="react-select"
              classNamePrefix="select"
            /> */}
            <small className="text-danger">
              {errors?.curriculumSectionId &&
                errors.curriculumSectionId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="publish">
              <FormattedMessage id="publish" />
            </Label>

            <Input
              type="select"
              name="publish"
              value={publish}
              onChange={(e) => setPublish(e.target.value)}
              id="publish"
              innerRef={register({ required: true })}
            >
              <option value="1">{intl.formatMessage({ id: "Allow" })}</option>
              <option value="0">
                {intl.formatMessage({ id: "Do not allow" })}
              </option>
            </Input>
          </FormGroup>

          <FormGroup>
            <Label for="status">
              <FormattedMessage id="Status" />
            </Label>

            <Input
              type="select"
              name="status"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
              id="status"
              innerRef={register({ required: true })}
            >
              <option value="0">{intl.formatMessage({ id: "Blocked" })}</option>
              <option value="1">{intl.formatMessage({ id: "Active" })}</option>
            </Input>
          </FormGroup>

          <div style={{ textAlign: "end", marginTop: "145px" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CourseDocumentTab);
