import { useEffect, useState } from "react";
import { AlignJustify, Book } from "react-feather";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import {
  Alert,
  Card,
  CardBody,
  Col,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap";
import { getQuiz } from "../store/action";
import CourseDocumentTab from "./CourseDocument";
import { getData as getDataProduct } from "../../../product/store/action";

const CourseDocumentEdit = () => {
  const [activeTab, setActiveTab] = useState("1"),
    store = useSelector((state) => state.quiz),
    dispatch = useDispatch(),
    { id } = useParams();
  const lang = useSelector((state) => state.common.language);

  const toggle = (tab) => setActiveTab(tab);

  useEffect(() => {
    dispatch(getQuiz(parseInt(id)));
  }, [dispatch]);

  useEffect(() => {
    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          lang: lang,
          product_type: 0,
        },
        order: [
          {
            key: "id",
            value: "desc",
          },
        ],
      })
    );
  }, [lang]);

  return store.selected !== null && store.selected !== undefined ? (
    <Row className="app-user-edit">
      <Col sm="12">
        <Card>
          <CardBody className="pt-2">
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === "1"} onClick={() => toggle("1")}>
                  <Book size={14} />
                  <span className="align-middle d-none d-sm-block">
                    <FormattedMessage id="Test" />
                  </span>
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId="1">
                <CourseDocumentTab selected={store.selected} store={store} />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </Col>
    </Row>
  ) : (
    <Alert color="danger">
      <h4 className="alert-heading">Not found</h4>
    </Alert>
  );
};
export default CourseDocumentEdit;
